.login_page{
    height: 100%;
    background: #2d3a4b;
    /* text-align: center; */
    color: #fff;
    display: flex;
    justify-content: center;
}

.login_h2{
    font-weight: bold;
    color: #fff;
    font-size: 36px;
}

.login_page > div{
    margin-top: 200px;

}
.login_page > div .ant-form{
    color: #fff;
    margin-top: 30px;
}
.login_page > div .ant-form-item-label > label{
    color: #fff;
}
.login_page > div .login-form {
    max-width: 300px;
}
.login_page > div .login-form-forgot {
  float: right;
}
.login_page > div .ant-col-rtl .login-form-forgot {
  float: left;
}
.login_page > div .login-form-button {
  width: 100%;
}