.ant-layout{
    height: 100%;
}
.trigger {
    padding: 0 24px;
    font-size: 18px;
    line-height: 64px;
    cursor: pointer;
    transition: color 0.3s;
  }
  
.trigger:hover {
    color: #1890ff;
  }
  
.logo {
    height: 32px;
    margin: 16px;
    background: rgba(255, 255, 255, 0.3);
    text-align: center;
    color: #f5f5f5;
    line-height: 32px;
    font-weight: bold;
    font-family: '楷体';
  }
  
  .site-layout .site-layout-background {
    background: #fff;
  }
  .ant-layout-header{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .logout{
    margin-right: 50px;
  }
  .tabs_box{
    width: 100%;
  }
  .title{
    width: 200px;
    margin: 0 auto;
    text-align: justify;
    text-align-last: justify;
    /* 这行必加，兼容ie浏览器 */
    text-justify:distribute-all-lines; 
  }
  .ant-tabs-top > .ant-tabs-nav, .ant-tabs-bottom > .ant-tabs-nav, .ant-tabs-top > div > .ant-tabs-nav, .ant-tabs-bottom > div > .ant-tabs-nav{
    margin: 0;
  }
  .footer{
    text-align: center;
    line-height: 30px;
  }